.signin_parent {
  /* width: 40em; */
  position: relative;
  height: 100vh;
  width: 100vw;
}

.signin_div {
  width: 80vw;
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  padding: 1em;
  background-color: aliceblue;
  border-radius: 1em;
}

@media screen and (min-width: 768px) {
  .signin_div {
    width: 30vw;
  }
}

.forms_div {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1em 2em;
}

.signin_header {
  font-size: 5em;
  font-weight: 500;
  padding: 0.2em;
}

.credentials--div {
  margin: 1em 2em;
  width: 100%;
}

/* .label {
  font-size: 1.3em;
  font-weight: 600;
  display: block;
} */

.input--field {
  font-family: inherit;
  width: 100%;
  box-sizing: border-box;
  font-size: 1.2em;
  padding: 1em;
  margin: 0.5em 0;
  box-shadow: rgba(0, 0, 0, 0.15) 0px 3px 8px;
  background-color: aliceblue;
  border-radius: 0.5em;
  outline-width: 0;
  border-width: 0;
}

.input--field:hover,
.input--field:focus {
  background-color: powderblue;
}

.button--div {
  width: 100%;
  margin: 1em 2em;
  background: linear-gradient(
    90deg,
    rgb(9, 216, 221) 0%,
    rgb(14, 80, 242) 100%
  );
  border-radius: 1em;
  transition: transform 0.2s;
}

.button {
  font-family: inherit;
  font-size: 2em;
  padding: 0.5em;
  background-color: inherit;
  color: aliceblue;
  border: 0px;
}

.button:hover {
  cursor: pointer;
}

.button--div:hover {
  cursor: pointer;
  background: linear-gradient(
    90deg,
    rgb(14, 86, 255) 0%,
    rgb(20, 86, 150) 100%
  );
  /* transform: scale(1.02); */
}

.button--div:active {
  transform: scale(0.95);
}

.register--div {
  font-family: inherit;
}

.register_route {
  cursor: pointer;
  font-size: 1.8em;
  transition: 0.5s;
}

.register_route:hover {
  opacity: 0.5;
  transition: opacity 0.5s;
}

.register_route:active {
  transform: scale(0.95) 0.5s;
}

.invalidCredentials {
  font-size: 1.5em;
  font-weight: bold;
  width: 100%;
  background-color: rgb(253, 219, 224);
  border: 0.1em solid rgb(252, 96, 96);
}
